import React from 'react';
import { ReactComponent as BackIcon } from "./assets/backIcon.svg";
import { ReactComponent as MorelIcon } from "./assets/moreIcon.svg";
import styles from "./style.module.css";
import CreatorProfile from "./components/profile";
import {useNavigate} from "react-router";


function PMHeader() : JSX.Element {

    const navigate = useNavigate();

    return (
        <div className={styles.header}>
            <div className={styles.buttons}>
                <a onClick={() => navigate('/', {replace: true})}><BackIcon/></a>
                    <p className={styles.headerText}>Private message</p>
                    <MorelIcon/>
            </div>
            <CreatorProfile/>
        </div>
    );
}

export default PMHeader;
