import React from 'react';
import styles from "./style.module.css";
import {useRecoilState} from "recoil";
import {uploadedFileState} from "../../../../../../../state/atoms";
import ReactPlayer from "react-player";

function VideoPreview() : JSX.Element {

    const [state, setUrl] = useRecoilState(uploadedFileState);

    const getBase64 = (file : Blob) => {
        return new Promise((resolve, reject) => {
            const reader = new FileReader();
            reader.onload = () => resolve(reader.result);
            reader.onerror = error => reject(error);
            reader.readAsDataURL(file);
        });
    }

    const imageUpload = (e : any) => {

        const file = e.target.files[0];
        getBase64(file).then(base64 => {
            // @ts-ignore
            setUrl({url: base64});
            console.debug("file stored", base64);
        });
    };

        function isImage() {
            const type = state.url.split('/');
            if (type[0] === 'data:image') {
                return 'image'
            }
            if (type[0] === 'data:video') {
                return 'video'
            }
        }
        console.log(state.url)

    return (
        <div className={styles.wrapper}>
            <div className={styles.video}>
                <input
                    className={styles.uploadField}
                    type="file"
                    id="imageFile"
                    name='imageFile'
                    onChange={imageUpload} />
                {state.url
                    ? isImage() === 'image'
                        ? <img src={state.url} className={styles.photoPreview}/>
                        : <ReactPlayer controls url={state.url} className={styles.videoPreview}/>
                    : <p>...Preparing</p>
                }

            </div>
            <div className={styles.chatIcon}>
                <span>Ji</span>
            </div>
        </div>
    );
}

export default VideoPreview;
