import React from 'react';
import Modal from 'react-modal';
import VideoControlsButton from "../video-control-buttons";
import { ReactComponent as CheckIcon } from "../../assets/checkIcon.svg";
import { ReactComponent as MessageIcon } from "../../assets/messageIcon.svg";
import { ReactComponent as LoaderIcon } from "../../assets/loaderIcon.svg";
import { ReactComponent as DoneIcon } from "../../assets/doneIcon.svg";
import { ReactComponent as ModalLoaderIcon } from "../../assets/modalLoaderIcon.svg";
import styles from './style.module.css';
import PayButton from "./components/pay-button";
import {useNavigate} from "react-router";

function PayModal() {
    const [modalIsOpen, setIsOpen] = React.useState(false);
    const [loader, setLoader] = React.useState(false);
    const [done, setDone] = React.useState(false);
    const [loadModal, setLoadModal] = React.useState(false);

    function openModal() {
        setLoadModal(true);
        setTimeout(() => setIsOpen(true), 400)
        setTimeout(() => setLoadModal(false), 400)
    }

    function closeModal() {
        setIsOpen(false);
    }

    const navigate = useNavigate();

    function handlePay() {
        setLoader(true);
        setTimeout(() => setDone(true), 600);
        setTimeout(() => setLoader(false), 800);
        setTimeout(() => setDone(false), 800);
        setTimeout(closeModal, 800);
        setTimeout(() => navigate('/start-conversation'), 800);
    }

    return (
        <div>
            <a className={styles.button} onClick={openModal}>
                <VideoControlsButton link="#" glowing={true} icon={ loadModal ? <ModalLoaderIcon className={styles.modalLoaderIcon}/> : <CheckIcon/>}/>
            </a>
            <Modal
                className={styles.modal}
                isOpen={modalIsOpen}
                onRequestClose={closeModal}
                contentLabel="Example Modal"
            >
                <p className={styles.title}>You are going to pay</p>
                <p className={styles.price}>$ 25.00 USD</p>
                <div className={styles.payTo}>to
                    <div className={styles.chatIcon}>
                        <span>Ol</span>
                    </div>
                    oli_sykes for
                    <MessageIcon className={styles.messageIcon}/>
                    private message</div>
                <p className={styles.refund}>
                    Note: No worries, your money will be refunded if there is a 7 days quiteness on the creator side.
                </p>
                <div className={styles.buttons}>
                    <a onClick={handlePay}><PayButton text="Pay with Apple" highlighted={true}/></a>
                    <PayButton text="Pay with Credit Card" highlighted={false}/>
                    <PayButton text="Other options" highlighted={false}/>
                </div>
                {loader && (
                    <div className={styles.loader}>
                        <div className={styles.rotateContainer}>
                            {!done
                                ? <LoaderIcon className={styles.loaderIcon}/>
                                : <DoneIcon className={styles.doneIcon}/>
                            }
                        </div>
                    </div>
                )}
            </Modal>
        </div>
    );
}
export default PayModal;