import React from 'react';
import { ReactComponent as ForwardIcon } from "../../assets/forwardIcon.svg";
import styles from "./style.module.css";
import {Link} from "react-router-dom";

type PrivateMessagesTypesButtonProps = {
    text : string,
    price : number,
    link : string
}

function PrivateMessageTypesButton(props : PrivateMessagesTypesButtonProps) : JSX.Element {
    return (
        <Link className={styles.link} to={props.link}>
            <div className={styles.button}>
                <p className={styles.text}>{props.text}</p>
                <p className={styles.price}>${props.price} <ForwardIcon className={styles.icon}/></p>
            </div>
        </Link>
    );
}

export default PrivateMessageTypesButton;
