import React from 'react';
import styles from './style.module.css';
import { ReactComponent as GroupIcon } from "./assets/groupIcon.svg";

function GroupCard() : JSX.Element {
    return (
        <div className={styles.wrapper}>
            <GroupIcon/>
            <div className={styles.info}>
                <div className={styles.title}>Group sessions</div>
                <div className={styles.price}><span>from </span>25$ <span>/month</span></div>
            </div>
            <div className={styles.button}>Start session</div>
        </div>
    );
}

export default GroupCard;
