import React from 'react';
import styles from './style.module.css';
import { ReactComponent as MessageIcon } from "./assets/messageIcon.svg";
import {Link} from "react-router-dom";

function MessageCard() : JSX.Element {
    return (
        <div className={styles.wrapper}>
            <MessageIcon/>
            <div className={styles.info}>
                <div className={styles.title}>Private message</div>
                <div className={styles.price}><span>from </span>25$</div>
            </div>
            <Link className={styles.link} to="/pm">
                <div className={styles.button}>Start chat</div>
            </Link>
        </div>
    );
}

export default MessageCard;
