import React from 'react';
import styles from "./style.module.css";
import {Link} from "react-router-dom";

type PrivateMessagesTypesButtonProps = {
    icon : JSX.Element,
    link : string,
    glowing ?: boolean
}

function VideoControlsButton(props : PrivateMessagesTypesButtonProps) : JSX.Element {
    return (
        <Link className={styles.link} to={props.link}>
            <div className={props.glowing ? styles.glowing : styles.button}>
                <div className={styles.icon}>{props.icon}</div>
            </div>
        </Link>
    );
}

export default VideoControlsButton;