import React from 'react';
import styles from "./style.module.css"
import {useRecoilState} from "recoil";
import {uploadedFileState} from "../../../../../../../state/atoms";
import ReactPlayer from "react-player";


function VideoPreview() : JSX.Element {

    const url = useRecoilState(uploadedFileState);
    console.log(url);


    function isImage() {
        const type = url[0].url.split('/');
        if (type[0] === 'data:image') {
            return 'image'
        }
        if (type[0] === 'data:video') {
            return 'video'
        }
    }

    return (
        <div className={styles.wrapper}>
            <div className={styles.video}>
                {url
                    ? isImage() === 'image'
                        ? <img src={url[0].url} className={styles.photoPreview}/>
                        : <ReactPlayer controls={true} url={url[0].url} className={styles.videoPreview}/>
                    : <p>...Preparing</p>
                }
                <p className={url[0].url ? styles.dateTextWhite : styles.dateText}>Tue, 9:41</p>
            </div>
            <div className={styles.chatIcon}>
                <span>Ji</span>
            </div>
        </div>
    );
}

export default VideoPreview;
