import React from "react";
import {
    BrowserRouter,
    Route,
} from "react-router-dom";
import Home from "../../pages/home";
import PrivateMessages from "../../pages/private-messages";
import AskQuestion from "../../pages/ask-question";
import VideoMessage from "../../pages/video-message";
import StartConversation from "../../pages/start-conversation";
// @ts-ignore
import SlideRoutes from 'react-slide-routes';

const WrapRouters = () : JSX.Element => {

    const pathList = [
        '/',
        '/pm',
        '/question',
        '/video-message',
        '/start-conversation'
    ]

    return (
        <BrowserRouter>
            <SlideRoutes duration={500} pathList={pathList}>
                <Route path="/" element={<Home/>}/>
                <Route path="/pm" element={<PrivateMessages/>}/>
                <Route path="/question" element={<AskQuestion/>}/>
                <Route path="/video-message" element={<VideoMessage/>}/>
                <Route path="/start-conversation" element={<StartConversation/>}/>
            </SlideRoutes>
        </BrowserRouter>
    )
}
export default WrapRouters;

