import React from 'react';

import styles from "./style.module.css";
import VideoControlsButton from "./components/video-control-buttons";
import { ReactComponent as AudioIcon } from "./assets/audioIcon.svg";
import { ReactComponent as VideoIcon } from "./assets/videoIcon.svg";
import { ReactComponent as MessageIcon } from "./assets/messageIcon.svg";

function VideoControls() : JSX.Element {
    return (
        <div className={styles.wrapper}>
            <p className={styles.text}>
                Tap Video button to upload your video. <br/>
                Video length limit is 30 seconds
            </p>
            <div className={styles.buttons}>
                <VideoControlsButton link="/video-message"  icon={<MessageIcon/>}/>
                <VideoControlsButton link="/video-message" glowing={true}  icon={<VideoIcon/>}/>
                <VideoControlsButton link="/video-message"  icon={<AudioIcon/>}/>
            </div>
        </div>
    );
}

export default VideoControls;
